import React from 'react'
import InfoEdit from "../edits/Information.json"
import "../css/information.css"
import Footer from '../components/Footer'
const Information = () => {
  return (
    <div className='InfoField'>
      <div className="Title">
        会社案内
      </div>
      <div className="GreetField">
        <div className="Title">{InfoEdit.greetTitle}</div>
        <div className="GreetContent">
          {InfoEdit.greets.map(text => <p key={text}>{text}</p>)}
          <div className="Representative">
            代表取締役 森口 正浩
          </div>
        </div>
      </div>
      <div className="Overview">
        <div className="Title">会社概要</div>
        {InfoEdit.overview.map(data => (
          <div className="Box" key={data.title}>
            <div className="Left">
              {data.title}
            </div>
            <div className="Right">
              {data.content}
            </div>
          </div>
        ))}
        </div>
        <div className="History">
          <div className="Title">{InfoEdit.historyName}</div>
          {
            InfoEdit.history.map(data =>
              <div className="Box" key={data.date}>
                <div className="Left">
                  {data.date}
                </div>
                <div className="Right">
                  {data.content}
                </div>
              </div>
            )
          }
        </div>
        <Footer />
      </div>
      )
}

      export default Information
