import React from 'react'
import Footer from '../components/Footer'
import "../css/recruit.css"
import editRecruit from "../edits/recruitment.json"

const Recruitment = () => {
  return (
    <div className='RecruitField'>
      <div className="Logo">
        {editRecruit.title}
      </div>
      <div className="Explanation">
        {editRecruit.explanation.map(text => <p key={text}>{text}</p>)}
      </div>
      <div className="RecruitBox">
        <div className="Box">
          <div className="Left">
            仕事内容
          </div>
          <div className="Right">
            {editRecruit.jobContent.map(text => <p key={text}>{text}</p>)}
          </div>
        </div>
        <div className="Box">
          <div className="Left">
            資格
          </div>
          <div className="Right">
            {editRecruit.license.map(text => <p key={text}>{text}</p>)}
          </div>
        </div>
        <div className="Box">
          <div className="Left">
            給与
          </div>
          <div className="Right">
            {editRecruit.salary.map(text => <p key={text}>{text}</p>)}
          </div>
        </div>
        <div className="Box">
          <div className="Left">
            時間・休日
          </div>
          <div className="Right">
            {editRecruit.times.map(text => <p key={text}>{text}</p>)}
          </div>
        </div>
        <div className="Box">
          <div className="Left">
            待遇
          </div>
          <div className="Right">
            {editRecruit.treatment.map(text => <p key={text}>{text}</p>)}
          </div>
        </div>
        <div className="Box">
          <div className="Left">
            勤務地
          </div>
          <div className="Right">
            {editRecruit.location.map(text => <p key={text}>{text}</p>)}
          </div>
        </div>
      </div>
      <Footer />
    </div>
  )
}

export default Recruitment
