import React, { useState } from "react";
import { stack as Menu } from "react-burger-menu";
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import EmailOutlinedIcon from '@mui/icons-material/EmailOutlined';
import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined';
import DescriptionOutlinedIcon from '@mui/icons-material/DescriptionOutlined';
import FacebookIcon from '@mui/icons-material/Facebook';
import "../css/burger.css"
import topEdit from "../edits/TopPage.json";
import { Link } from "react-router-dom";

const BurgerMenu = ({ width }) => {

    const [open, setOpen] = useState(false)

    return (
        <Menu {...width} right isOpen={open}
            onStateChange={(e) => {
                setOpen(e.isOpen);
            }}>
            <div className="MenuField">

                <div className="Menu" onClick={()=> setOpen(false)}>
                    <m><Link to="/morguchifoods/"><HomeOutlinedIcon style={{ fontSize: "22px", marginTop: "3px", marginRight: "5px" }} />{topEdit.menu.home}</Link></m>
                </div>
                <div className="Menu" onClick={()=> setOpen(false)}>
                    <m><Link to="/morguchifoods/information"><InfoOutlinedIcon style={{ fontSize: "22px", marginTop: "3px", marginRight: "5px" }} />{topEdit.menu.information}</Link></m>
                </div>
                <div className="Menu" onClick={()=> setOpen(false)}>
                    <m><Link to="/morguchifoods/workcontent"><DescriptionOutlinedIcon style={{ fontSize: "22px", marginTop: "3px", marginRight: "5px" }} />{topEdit.menu.workContent}</Link></m>
                </div>
                <div className="Menu" onClick={()=> setOpen(false)}>
                    <m><Link to="/morguchifoods/contact"><EmailOutlinedIcon style={{ fontSize: "22px", marginTop: "3px", marginRight: "5px" }} />{topEdit.menu.contact}</Link></m>
                </div>
            </div>
            <div className="ButtomField">
                <div className="Link">
                    <a href={topEdit.facebookURL}>
                        <FacebookIcon style={{ fontSize: "22px", color: "blue" }} />
                    </a>
                </div>
                <div className="CopyRight">
                    {topEdit.copyright}
                </div>
            </div>
        </Menu>
    );
};

export default BurgerMenu;
