import React, { useRef, useState } from 'react'
import "../css/contact.css"
import validator from "validator"
import Footer from '../components/Footer';

const Contact = () => {
  const nameRef = useRef(null);
  const mailRef = useRef(null);
  const contentRef = useRef(null);
  const [name, setName] = useState();
  const [email, setEmail] = useState();
  const [content, setContent] = useState();
  const [error, setError] = useState(
    {
      name: "",
      email: "",
      content: ""
    }
  )

  const submit = () => {
    var mailErr = mailRef.current.value === "" ? "メールアドレスが未入力です" : "";

    setError({
      name: nameRef.current.value === "" ? "お名前が未入力です" : "",
      email: mailErr === "" && !validator.isEmail(mailRef.current.value) ? "メールアドレスが無効です" : mailErr,
      content: contentRef.current.value === "" ? "お問い合わせ内容が未入力です" : ""
    })
    if (nameRef.current.value !== "" && validator.isEmail(mailRef.current.value) && contentRef.current.value !== "") {
      alert("success")
    }
  }

  return (
    <div className='ContactField'>
      <div className="Logo">
        お問い合わせ
      </div>
      <form className="FormField" onSubmit={() => submit()}>
        <div className='FormContent'>
          <div className="label">お名前</div>
          <input type="text" ref={nameRef} className="name" id="name" value={name} onChange={() => setName(nameRef.current.value)} />
          <div className='error'>{error.name}</div>
        </div>
        <div className='FormContent'>
          <div className="label">メールアドレス</div>
          <input type="text" ref={mailRef} className="email" id="email" value={email} onChange={() => setEmail(mailRef.current.value)} />
          <div className='error'>{error.email}</div>
        </div>
        <div className='FormContent'>
          <div className="label">お問い合わせ内容</div>
          <textarea type="text" ref={contentRef} className="content" id="content" value={content} onChange={() => setContent(contentRef.current.value)} />
          <div className='error'>{error.content}</div>
        </div>
        <div className="submit" onClick={() => submit()}>送信</div>
      </form>
      <Footer/>
    </div>
  )
}

export default Contact
