import React from 'react'
import "../css/footer.css"
import FacebookIcon from '@mui/icons-material/Facebook';
import topEdit from "../edits/TopPage.json";
import ContactMailOutlinedIcon from '@mui/icons-material/ContactMailOutlined';
import LocalPhoneOutlinedIcon from '@mui/icons-material/LocalPhoneOutlined';
import FaxOutlinedIcon from '@mui/icons-material/FaxOutlined';

const Footer = () => {
    return (
        <div className='FooterField'>
            <div className="Information">
                <div className="Logo">有限会社モリグチフーズ</div>
                <div className="Content">
                    <div className="icon">
                        <ContactMailOutlinedIcon fontSize='small'/>
                    </div>
                    <div className="contents">
                        <p> 〒286-0014 千葉県成田市郷部290-4</p>
                    </div>
                    <div className="icon">
                        <LocalPhoneOutlinedIcon fontSize='small'/>
                    </div>
                    <div className="contents">
                        <p>0476-22-4797</p>
                    </div>
                    <div className="icon">
                        <FaxOutlinedIcon fontSize='small'/>
                    </div>
                    <div className="contents">
                        <p>0476-22-4798</p>
                    </div>
                </div>
            </div>
            <div className="Link">
                <a href={topEdit.facebookURL}>
                    <FacebookIcon style={{ fontSize: "22px", color: "blue" }} />
                </a>
            </div>
            <div className="CopyRight">
                {topEdit.copyright}
            </div>
        </div>
    )
}

export default Footer
