import React from 'react'
import "../css/header.css"
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import EmailOutlinedIcon from '@mui/icons-material/EmailOutlined';
import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined';
import DescriptionOutlinedIcon from '@mui/icons-material/DescriptionOutlined';
import BusinessCenterOutlinedIcon from '@mui/icons-material/BusinessCenterOutlined';
import BurgerMenu from './BurgerMenu';
import topEdit from "../edits/TopPage.json";
import { Link,useNavigate } from 'react-router-dom';
import { animateScroll as scroll } from "react-scroll";

import logo from "../img/logo1.png"
const Header = () => {
    const navigate = useNavigate();
    const handlePage = (path) =>{
        scroll.scrollToTop();
        navigate(path);
    }
    return (
        <div className='HeaderField'>
            <div className="Burger">
                <BurgerMenu width={250} />
            </div>
            <div className="Logo">
                {<img src={logo} alt=""/>}
            </div>
            <div className="MenuField">
                <div className="Menu" onClick={()=> handlePage("/morguchifoods/")}>
                    <HomeOutlinedIcon style={{ fontSize: "25px", marginTop: "3px", marginRight: "5px" }} />{topEdit.menu.home}
                </div>
                <div className="Menu" onClick={()=> handlePage("/morguchifoods/information")}>
                    <InfoOutlinedIcon style={{ fontSize: "25px", marginTop: "3px", marginRight: "5px" }} />{topEdit.menu.information}
                </div>
                <div className="Menu" onClick={()=> handlePage("/morguchifoods/workcontent")}>
                    <DescriptionOutlinedIcon style={{ fontSize: "25px", marginTop: "3px", marginRight: "5px" }} />{topEdit.menu.workContent}
                </div>
                <div className="Menu" onClick={() => handlePage("/morguchifoods/contact")}>
                    <EmailOutlinedIcon style={{ fontSize: "25px", marginTop: "3px", marginRight: "5px" }} />{topEdit.menu.contact}
                </div>
            </div>
        </div>
    )
}

export default Header
