import React from 'react'
import "../css/work.css"
import honenuki from "../img/honenuki.jpg"
import seikei from "../img/seikei.jpg"
import housou from "../img/housou.jpg"
import keiryou from "../img/keiryou.jpg"
import Footer from '../components/Footer'
import workEdit from "../edits/workcontent.json";

const WorkContent = () => {
    return (
        <div className='WorkField'>
            <div className="Top">
                {workEdit.title}
            </div>
            <div className="Explanation">
                {workEdit.explanation.map(exp => <p key={exp}>{exp}</p>)}
            </div>
            <div className="Box">
                <div className="Left">
                    <img src={honenuki} alt="" />
                </div>
                <div className="Right">
                    <h1>骨抜き</h1>
                    {workEdit.honenuki.map(exp => <p key={exp}>{exp}</p>)}
                </div>
            </div>
            <div className="Box">
                <div className="Left">
                    <img src={seikei} alt="" />
                </div>
                <div className="Right">
                    <h1>整形</h1>
                    {workEdit.seikei.map(exp => <p key={exp}>{exp}</p>)}
                </div>
            </div>
            <div className="Box">
                <div className="Left">
                    <img src={housou} alt="" />
                </div>
                <div className="Right">
                    <h1>包装</h1>
                    {workEdit.housou.map(exp => <p key={exp}>{exp}</p>)}
                </div>
            </div>
            <div className="Box">
                <div className="Left">
                    <img src={keiryou} alt="" />
                </div>
                <div className="Right">
                    <h1>計量</h1>
                    {workEdit.keiryou.map(exp => <p key={exp}>{exp}</p>)}
                </div>
            </div>
            <Footer/>
        </div>
    )
}

export default WorkContent
