import React from 'react'
import TopSlider from '../components/TopSlider'
import "../css/top.css"
import Footer from "../components/Footer";
import { Scroll } from "../components/ScrollAnimation";
import info from "../img/info.png"
import work from "../img/work.png"
import adopt from "../img/adopt.png"
import topEdit from "../edits/TopPage.json";
import { animateScroll as scroll } from "react-scroll";
import { Link, useNavigate } from 'react-router-dom';

const TopPage = () => {
    const navigate = useNavigate();
    const handlePage = (path) => {
        scroll.scrollToTop();
        navigate(path);
    }
    return (
        <>
            <div className='TopField'>
                <div className="Slider">
                    <TopSlider />
                    <div className="Message">
                        <Scroll>
                            <h1>{topEdit.rightText.headline}</h1>
                            {topEdit.rightText.lines.map(text => <p key={text}>{text}</p>)}
                        </Scroll>
                    </div>
                </div>
            </div>
            <div className="TopInformationField">
                <Scroll>
                    <div className="Logo">
                        <p>経営理念</p>
                    </div>
                    <div className="Image">
                        <img src={info} alt="" />
                    </div>
                    <div className="Message">
                        <div className="JumpBox" onClick={() => handlePage("/morguchifoods/information")}>
                            会社案内
                        </div>
                    </div>
                </Scroll>
            </div>
            <div className="MiddleMenu">
                <Scroll>
                    <table>
                        <tr>
                            <td>
                                <div className="TopBusinessContentField" onClick={() => handlePage("/morguchifoods/workcontent")}>
                                    <img src={work} alt="" />
                                </div>
                            </td>
                        </tr>
                    </table>
                </Scroll>
            </div>
            <Footer />
        </>
    )
}

export default TopPage
